import YCPDpng from './../../assets/YCPD.png';
import VerifiCodePng from './../../assets/Verification-Code.png';
import VerifiTipePng from './../../assets/Verification-Tip.png';

import GVerify from './../../components/GVerify.js';
import loadPageVar from './../../utils/loadPageVar';
import request from './../../utils/request';
import ajaxs from './ajaxs.js';

import { Indicator } from 'mint-ui';
import 'mint-ui/lib/style.min.css';

export default {
  name: 'security',

  data () {
    return {
      picture: { // 
        YCPDpng: YCPDpng,             // logo
        VerifiCodePng: VerifiCodePng, // 
        VerifiTipePng: VerifiTipePng, // 16 ()
      },

      urlParam: '',              // URL  

      securityCode: '',          // 
      securityCodeError: '',     // 
      
      verificationCode: '',      // 
      verificationCodeError: '', // 
      
      verifyCodeComponent: null,          // 

      isLoadByqrCode: false,  // ,    
      isLoadedProduct: false, // 
      loadedErrorMsg: '',     // 

      qrCode: {
        UnitCode: 'code', // ae6e4931-2d0f-4453-9cd5-09be5fd5e3d8
        Name: '', // 
        StoreName: '', // 
        SupplyDate: '', // 
        StringVal2: '', // 
        IntVal9: 4, // 1
        IntVal10: 4, // 2
        StringVal4: '', // 2 
        ProductionAddress: '', // 
        Manufacturer: '',      // 
        DeclaringCustomsID: '51662016666201885', // 
        DeclaringCustomsImg: 'img.jpg',
        QualityTesting: 'PDS Castrol Magnatec 5W-30 C3', // 
        QualityTestingImg: 'img.jpg',
        ProductData: 'Magnatec 5W-30 C3', // 
        ProductDataImg: 'img.jpg',
        ProductID: '170902010000013090'
      }
    }
  },

  methods: {
    verifyInput: function () { // 
      if (this.securityCode === '') {
        this.securityCodeError = '';
        return request.error('!');
      }

      if (this.verifyCodeComponent.validate(this.verificationCode) === false) {
        this.verificationCodeError = '';
        return request.error('!');
      }

      return request.success();
    },

    securitySubmit: function () { // 
      let _this = this;
      let verifySubmit = this.verifyInput();

      const solveQRcodeData = qrCodeData => { // 
        _this.qrCode = qrCodeData[0];
      }

      if (verifySubmit.result !== 1) { // 
        alert(verifySubmit.message);

      } else {
        Indicator.open('...');

        ajaxs.GetBarcodeDetail(this.securityCode)
        .then(val => {
          solveQRcodeData(val);          // 
          _this.verifyCodeComponent.refresh(); // 
          _this.isLoadedProduct = true;
          Indicator.close();
        }, error => {
          _this.isLoadedProduct = true;
          _this.loadedErrorMsg = error;
          Indicator.close();
          alert(error);
        });
      }
    },

    initGVerify: function () { // 
      this.verifyCodeComponent = new GVerify({
        id: "gVerifyContainer",
        width: "150",
        height: "42",
      });
    },

    initUrlParam: function () { //  
      const _this = this;

      this.urlParam = loadPageVar('id');

      const solveQRcodeData = qrCodeData => { // 
        _this.securityCode = qrCodeData[0].UnitCode;
        _this.qrCode = qrCodeData[0];
      }

      if (this.urlParam) { // , !
        this.isLoadByqrCode = true; //  
        Indicator.open('...');

        ajaxs.getQRcodeBy(this.urlParam)
        .then(val => {
          solveQRcodeData(val); // 
          _this.isLoadedProduct = true; // 
          Indicator.close();
        }, error => {
          _this.securityCode = _this.urlParam;
          _this.isLoadedProduct = true;
          _this.loadedErrorMsg = error;
          Indicator.close();
          alert(error);
        });

        return true
      } else {
        this.isLoadByqrCode = false; //  
        return false
      }
    }
  },

  watch: {
    securityCode: function (newSecurityCode, oldSecurityCode) {
      this.securityCodeError = ''; // 
    },

    verificationCode: function (newVerificationCode, oldVerificationCode) {
      this.verificationCodeError = ''; // 
    },
  },

  mounted: function() {
    if (this.initUrlParam() === false) { //  
      this.initGVerify(); // , 
    }
  }
}




