import config from './../../config/index.js';

const ajaxs = {
    getQRcodeBy(id) { //  
        let result = [{ // 
            UnitCode: "ae6e4931-2d0f-4453-9cd5-09be5fd5e3d8",
            Name: "SN5W40",
            StoreName: "",
            SupplyDate: "",
            StringVal2: "",
            IntVal9: 4,
            IntVal10: 4,
            StringVal4: "",
            ProductionAddress: "",
            Manufacturer: "",
            DeclaringCustomsID: "51662016666201885",
            DeclaringCustomsImg: "http://p6ygud9kn.bkt.clouddn.com/YCPD/DeclaringCustomsID.jpg",
            QualityTesting: "PDS Castrol Magnatec 5W-30 C3",
            QualityTestingImg: "http://p6ygud9kn.bkt.clouddn.com/YCPD/QualityTesting.jpg",
            ProductData: "Magnatec 5W-30 C3",
            ProductDataImg: "http://p6ygud9kn.bkt.clouddn.com/YCPD/ProductData.jpg",
            ProductID: "170902010000013090"
        }];
        return new Promise((resolve, reject) => {
            // resolve(result);

            // 9e3ff5ac-b201-4d3c-99a6-ff60621147e6
            fetch(`${config.basicUrl}/QRCode/GetQRcodeDetail?id=${id}`, {
            // fetch('http://192.168.3.235:9081/QRCode/GetQRcodeDetail?id=b85fb522-67d1-4b52-a95f-01b400c4a1d7', {
                'method': 'GET',
                'headers': {
                    'Content-Type': 'application/json; charset=utf-8',
                },
            }).then(
                response => response.json(),
                error => ({Code: '1', Msg: error})
            ).then(val => {
                if (val.Code === 200) {
                    if (val.Msg === '') {
                        resolve(val.Data);
                    } else {
                        reject(`, : ${val.Msg}`);
                    }
                } else {
                    reject(`, : ${val.Msg}`);
                }
            }).catch(error => reject(`, : ${error}`));
        });
    },

    GetBarcodeDetail(id) { //   
        let result = [{ // 
            Name: "",
            IntVal9: 0,
            IntVal10: 0,
            StringVal2: "",
            StringVal4: "",
            Manufacturer: "",
            ProductionAddress: "",
            DeclaringCustomsID: "51662016666201885",
            DeclaringCustomsImg: "http://p6ygud9kn.bkt.clouddn.com/YCPD/DeclaringCustomsID.jpg",
            ProductData: "Magnatec 5W-30 C3",
            ProductDataImg: "http://p6ygud9kn.bkt.clouddn.com/YCPD/QualityTesting.jpg",
            QualityTesting: "PDS Castrol Magnatec 5W-30 C3",
            QualityTestingImg: "http://p6ygud9kn.bkt.clouddn.com/YCPD/ProductData.jpg",
            ProductID: "171113010000110305",
        }];
        return new Promise((resolve, reject) => {
            // resolve(result);
            
            // aaa180505010000503924
            fetch(`${config.basicUrl}/QRCode/GetBarcodeDetail?id=${id}`, {
                'method': 'GET',
                'headers': {
                    'Content-Type': 'application/json; charset=utf-8',
                },
            }).then(
                response => response.json(),
                error => ({Code: '1', Msg: error})
            ).then(val => {
                if (val.Code === 200) {
                    if (val.Msg === '') {
                        resolve(val.Data);
                    } else {
                        reject(`, : ${val.Msg}`);
                    }
                } else {
                    reject(`, : ${val.Msg}`);
                }
            }).catch(error => reject(`, : ${error}`));
        });
    },
}

export default ajaxs;



